/* 注册协议：底层MQTT协议，上层用16进制数字字符协议
 总体格式：指令 + 空格 + 内容
 指令：4字节16进制字符
 内容：由多个字段组合而成，每个字段的格式：长度4B+字段代码2B+内容
 长度4B是16进制字符，例如004E，等于2+内容的长度
 字段代码2B是16进制字符，例如05
 多字段首尾相接，字段没有先后顺序，也可以缺少几个字段。
*/

//1、获取验证码操作
